import { useEffect } from 'react'
import { useState } from 'react'
import {
  AnagramaMetaData,
  getRachacucaAnagramGamesInOrder,
} from '../features/anagramaGame/AnagramAPI'
import { GameListItemComponent } from './game-list-item'

export function GameListComponent() {
  const [anagramas, setAnagramas] = useState<AnagramaMetaData[] | null>(null)
  const [error, setError] = useState<string | null>(null)
  useEffect(() => {
    ;(async () => {
      try {
        setAnagramas(await getRachacucaAnagramGamesInOrder())
      } catch (e) {
        setError('Não foi possível carregar a lista de jogos.')
      }
    })()
  }, [])
  if (error) {
    return <div>{error}</div>
  }
  if (anagramas) {
    return (
      <div className="d-flex flex-column">
        {anagramas.map(GameListItemComponent)}
      </div>
    )
  }
  return <div>Carregando...</div>
}
