import React from 'react'
import { useSelector } from 'react-redux'
import { useAppDispatch } from '../app/hooks'
import {
  SaveGameThunk,
  selectAnagramaWord,
  shuffleLetters,
} from '../features/anagramaGame/anagramaGameSlice'

import { AnagramaLetterComponent } from './letter'

export function AnagramaLettersComponent() {
  const dispatch = useAppDispatch()
  const word = useSelector(selectAnagramaWord)
  const { letters } = word
  const handleShuffle = () => {
    dispatch(shuffleLetters()).then(() => {
      dispatch(SaveGameThunk())
    })
  }
  return (
    <div className="d-flex flex-row text-center my-3">
      <div className="row flex-fill">
        <div className="col">
          {letters.map((letter) => {
            return (
              <AnagramaLetterComponent
                key={letter.letter + letter.index}
                {...letter}
              />
            )
          })}
        </div>
      </div>

      <button className="btn btn-outline-warning mt-2" onClick={handleShuffle}>
        Misturar
      </button>
    </div>
  )
}
