import React from 'react'
import ClassNames from 'classnames'
import { useAppDispatch } from '../app/hooks'
import {
  SaveGameThunk,
  selectLetter,
} from '../features/anagramaGame/anagramaGameSlice'
import { AnagramaLetter } from '../interfaces'

export function AnagramaLetterComponent(props: AnagramaLetter) {
  const dispatch = useAppDispatch()

  const { letter, index, used } = props
  const handleClick = (ev: React.MouseEvent<HTMLButtonElement>) => {
    if (used) return;
    ev.currentTarget.disabled = true;
    ev.currentTarget.disabled = false;
    dispatch(selectLetter({ letter, index }))
    dispatch(SaveGameThunk())

  }

  return (
    <button
      className={ClassNames([
        "m-1 fs-1 btn btn-lg btn-outline-dark shadow letter-box",
        {"active": used}
      ])}
      style={{
        width: '2em',
        height: '2em',
        lineHeight: '2em',
        textAlign: 'center',
        verticalAlign: 'middle',
        padding: '0',
        //fontSize: '1.2em',
      }}
      //disabled={used}
      onClick={handleClick}
    >
      {letter.toUpperCase()}
    </button>
  )
}
