import React, { useCallback, useEffect } from 'react'
import { useSelector } from 'react-redux'
import { useAppDispatch } from '../app/hooks'
import {
  clearLetters,
  SaveGameThunk,
  selectAnagramaSolutions,
  selectLetter,
} from '../features/anagramaGame/anagramaGameSlice'
import { BuyModeButtonComponent } from './buy-mode-button'
import { AnagramaLettersComponent } from './letters'
import { AnagramaSolutionsComponent } from './solutions'
import { AnagramaWordComponent } from './word'

export function AnagramaGameComponent() {
  const dispatch = useAppDispatch()
  const solutions = useSelector(selectAnagramaSolutions)
  const total = solutions.length
  const solved = solutions.filter((s) => s.solved).length
  const handleKeyDown = useCallback(
    (evt: KeyboardEvent) => {
      if (evt.key.length === 1) {
        const letter = evt.key
          .normalize('NFD')
          .replace(/[\u0300-\u036f]/g, '')
          .toUpperCase()
        dispatch(selectLetter({ letter }))
        dispatch(SaveGameThunk())
      } else if (evt.key === 'Backspace') {
        dispatch(clearLetters())
        dispatch(SaveGameThunk())
      }
    },
    [dispatch]
  )
  useEffect(() => {
    window.addEventListener('keydown', handleKeyDown)
    return () => {
      window.removeEventListener('keydown', handleKeyDown)
    }
  }, [handleKeyDown])
  return (
    <div className="container flex-fill d-flex flex-column">
      <div className="row card flex-fill bg-light d-flex my-2">
        <div className="card-header py-0">
        <div className="row">
          <h5 className="col-6 my-3">
            Palavras ({solved}/{total})
          </h5>
          <div className="col-6 my-2">
            <BuyModeButtonComponent />
          </div>
        </div>
        </div>
        <div className="row flex-fill" style={{ position: 'relative' }}>
          <div
            className="overflow-auto h-100 w-100 d-flex flex-column p-2"
            style={{ position: 'absolute' }}
          >
            <AnagramaSolutionsComponent />
          </div>
        </div>
      </div>

      <div className="row">
        <div className="col text-center h1">
          <AnagramaWordComponent />
        </div>
      </div>
      <div className="row">
        <div className="col text-center h1">
          <AnagramaLettersComponent />
        </div>
      </div>
    </div>
  )
}
