import React from 'react'
import { HashRouter as Router, Switch, Route } from 'react-router-dom'
import './App.css'
import { GameListComponent } from './components/game-list'
import { GameLoaderComponent } from './components/game-loader'
import { LatestGameComponent } from './components/latest-game'
import { LatestSaveComponent } from './components/latest-save'
import { MainComponent } from './components/main'
import { TopMenuComponent } from './components/top-menu'

function hasTouch() {
  return 'ontouchstart' in document.documentElement
         || navigator.maxTouchPoints > 0
}

function App() {
  if (!hasTouch()) document.body.className += ' hasHover'
  return (
    <Router>
      <div className="container-fluid h-100 d-flex flex-column">
        <div className="row">
          <div className="col">
            <TopMenuComponent />
          </div>
        </div>
        <div className="row flex-fill">
          <div className="col d-flex flex-column">
            <Switch>
              <Route path="/" exact>
                <MainComponent />
              </Route>
              <Route path="/game/continue" exact>
                <LatestSaveComponent />
              </Route>
              <Route path="/game/list" exact>
                <GameListComponent />
              </Route>
              <Route path="/game/latest" exact>
                <LatestGameComponent />
              </Route>
              <Route path="/game/:anagrama" component={GameLoaderComponent}/>
            </Switch>
          </div>
        </div>
      </div>
    </Router>
  )
}

export default App
