import React from 'react'
import { Button } from 'react-bootstrap'
import { useSelector } from 'react-redux'
import { useAppDispatch } from '../app/hooks'
import {
  clearLetters,
  SaveGameThunk,
  selectAnagramaWord,
} from '../features/anagramaGame/anagramaGameSlice'

export function AnagramaWordComponent() {
  const dispatch = useAppDispatch()
  const word = useSelector(selectAnagramaWord)
  const { currentWord } = word
  const handleClear = () => {
    dispatch(clearLetters())
    dispatch(SaveGameThunk())
  }
  return (
    (word.currentWord !== '') ? (<div className="d-flex flex-row border border-info text-info">
    <div className="row flex-fill p-0 m-0">
      <div className="col text-center my-2">{currentWord}&nbsp;</div>
    </div>
    {word.currentWord !== '' ? (<div className="row">
      <div className="col">
        <Button
          type="button"
          className="btn-close float-end my-3 me-2"
          onClick={handleClear}
        />
      </div>
    </div>) : null}

  </div>) : null)
}
