import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import { RootState } from '../../app/store'

export interface ShopState {
  buyLetterMode: boolean
}

const initialState: ShopState = {
  buyLetterMode: false
}

export const shopSlice = createSlice({
  name: 'shop',
  initialState,
  // The `reducers` field lets us define reducers and generate associated actions
  reducers: {
    setBuyMode: (state, action: PayloadAction<boolean>) => {
      state.buyLetterMode = action.payload
    },
    toggleBuyMode: (state) => {
      state.buyLetterMode = !state.buyLetterMode
    },
  },
})

export const {
  setBuyMode,
  toggleBuyMode
} = shopSlice.actions

export const selectShop = (state: RootState) => state.shop
export const selectBuyMode = (state: RootState) => state.shop.buyLetterMode


export default shopSlice.reducer
