import { configureStore, ThunkAction, Action } from '@reduxjs/toolkit'
import anagramaGameReducer from '../features/anagramaGame/anagramaGameSlice'
import shopReducer from '../features/anagramaGame/shopSlice'
import configReducer from '../features/anagramaGame/configSlice'

export const store = configureStore({
  reducer: {
    anagramaGame: anagramaGameReducer,
    shop: shopReducer,
    config: configReducer,
  },
})

export type AppDispatch = typeof store.dispatch
export type RootState = ReturnType<typeof store.getState>
export type AppThunk<ReturnType = void> = ThunkAction<
  ReturnType,
  RootState,
  unknown,
  Action<string>
>
