import React from 'react'
import ClassNames from 'classnames'
import { AnagramaSolution } from '../interfaces'
import { useSelector } from 'react-redux'
import { selectBuyMode, setBuyMode } from '../features/anagramaGame/shopSlice'
import {
  buySolutionLetter,
  SaveGameThunk,
} from '../features/anagramaGame/anagramaGameSlice'
import { useAppDispatch } from '../app/hooks'

export interface SolutionComponentProps {
  index: number
  solution: AnagramaSolution
}

export function AnagramaSolutionComponent(props: SolutionComponentProps) {
  const { solution, solved, partial } = props.solution
  const { index } = props
  const buyMode = useSelector(selectBuyMode)
  const dispatch = useAppDispatch()

  function getString() {
    if (solved) {
      return solution.toUpperCase()
    } else {
      if (partial) {
        return partial
      } else {
        return '?'.repeat(solution.length)
      }
    }
  }

  const handleBuy = () => {
    dispatch(buySolutionLetter(solution)).then(() => {
      dispatch(setBuyMode(false))
      dispatch(SaveGameThunk())
    })
  }


  return (<div className="col-3 col-md-2 d-grid">
  <button
    id={"sol" + index}
    onClick={handleBuy}
    disabled={!buyMode || solved}
    className={ClassNames([
      'btn',
      {'btn-outline-primary': buyMode && !solved},
      {'btn-success': solved},
      {'btn-outline-danger': partial && !solved},
    ])}
    //style={{ lineBreak: 'loose', maxWidth: '10em', display: 'block' }}
  >
    {getString()}
  </button>
</div>)
}
