import axios from 'axios'
import _ from 'lodash'
import {
  AnagramaGame,
  RachaCucaAnagramas,
  RachaCucaAnagramas2021,
  RachaCucaAnagramasIndex,
} from '../../interfaces'
import { IsSaved, SavedMetadata } from './SavedGame'

export async function getRachacucaAnagrama(normalizedAnagrama: string) {
  const response = await axios.get<RachaCucaAnagramas | RachaCucaAnagramas2021>(
    `${
      process.env.PUBLIC_URL
    }/dump/${normalizedAnagrama}.json?timestamp=${new Date().getTime()}`
  )
  if (response.data) {
    if ((response.data as RachaCucaAnagramas2021).slug) {
      return ConvertAnagrama2021(response.data as RachaCucaAnagramas2021)
    }
  }
  return response.data as RachaCucaAnagramas
}

export function ConvertAnagrama2021(anagrama: RachaCucaAnagramas2021): RachaCucaAnagramas {
  return {
    id: -1,
    palavra: normalize_anagram(anagrama.word),
    palavras: anagrama.words,
    palavras_normalized: anagrama.words.map((w) => normalize_word(w))
  }
}


export async function getRachacucaAnagramaIndex() {
  const response = await axios.get<RachaCucaAnagramasIndex>(
    `${
      process.env.PUBLIC_URL
    }/dump/index.json?timestamp=${new Date().getTime()}`
  )
  return response.data
}

export interface AnagramaMetaData extends SavedMetadata {
  anagrama: string
  date?: string
}

export async function getRachacucaAnagramGamesInOrder(): Promise<
  AnagramaMetaData[]
> {
  const index = await getRachacucaAnagramaIndex()
  const result: AnagramaMetaData[] = []
  index.byDate.reverse().forEach((value) => {
    if (!result.find((item) => value.anagram === item.anagrama)) {
      result.push({ ...IsSaved(value.anagram), anagrama: value.anagram, date: value.date })
    }
  })
  index.anagrams.forEach((value) => {
    if (!result.find((item) => value === item.anagrama)) {
      result.push({ ...IsSaved(value), anagrama: value })
    }
  })
  return result
}
export async function getLastRachacucaGame() {
  const games = await getRachacucaAnagramaIndex()
  return games.byDate[games.byDate.length - 1].anagram
}

export function convertRachacucaToAnagramaGame(
  rcAnagrama: RachaCucaAnagramas
): AnagramaGame {
  const gameData: AnagramaGame = {
    word: {
      anagram: rcAnagrama.palavra,
      currentWord: '',
      letters: _.shuffle(rcAnagrama.palavra.split('')).map((letter, index) => ({
        letter: letter,
        used: false,
        index,
      })),
    },
    solutions: rcAnagrama.palavras.map((palavra, index) => ({
      solution: palavra,
      normalizedSolution: rcAnagrama.palavras_normalized[index],
      solved: false,
    })),
  }
  return gameData
}


export function normalize_word(word: string): string {
  return word.normalize("NFD").replace(/[\u0300-\u036f]/g, "").toLowerCase();
}
export function normalize_anagram(anagram: string): string {
  return normalize_word(anagram).split("").sort().join("");
}
