import React from 'react'
import { NavLink } from 'react-router-dom'
import { AnagramaMetaData } from '../features/anagramaGame/AnagramAPI'
import ClassNames from 'classnames'

export interface GameListItemComponentProps extends AnagramaMetaData {}

export function GameListItemComponent(props: GameListItemComponentProps) {
  const anagrama = props
  const date = anagrama.date ? new Date(anagrama.date).toLocaleDateString() : "Desconhecido"
  return (
    <div className="row" key={anagrama.anagrama}>
      <div className="col my-1">
        <div
          className={ClassNames([
            'card',
            'text-light',
            {
              'bg-primary': anagrama.saved,
              'bg-secondary': !anagrama.saved,
              'bg-success': anagrama.solved,
            },
          ])}
        >
          <div className="card-header">
            {' '}
            {anagrama.anagrama.toUpperCase()} ({anagrama.anagrama.length} Letras)
            <NavLink
              className="btn text-light float-end m-0 p-0"
              to={`/game/${anagrama.anagrama}`}
            >
              Jogar
            </NavLink>
          </div>
          <div className="card-body">
            <ul>
              <li>Data: {date}</li>
              <li>Salvo: {anagrama.saved ? 'Sim' : 'Não'}</li>
              <li>Finalizado: {anagrama.solved ? 'Sim' : 'Não'}</li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  )
}
