import { createAsyncThunk, createSlice, PayloadAction } from '@reduxjs/toolkit'
import { RootState, AppThunk } from '../../app/store'
import { AnagramaGame, AnagramaLetter } from '../../interfaces'
import _ from 'lodash'
import { SaveGame } from './SavedGame'

export const SaveGameThunk = (): AppThunk => async (dispatch, getState) => {
  SaveGame(getState().anagramaGame)
}

export const shuffleLetters = createAsyncThunk<
  AnagramaLetter[],
  void,
  {
    // Optional fields for defining thunkApi field types
    state: RootState
  }
>('anagramas/shuffleLetters', async (_arg, thunkAPI) => {
  const letters = thunkAPI.getState().anagramaGame.word.letters
  return _.shuffle(letters).map((l, index) => ({ ...l, index }))
})


export const buySolutionLetter = createAsyncThunk(
  'anagramas/buySolutionLetter',
  async (solution: string, thunkAPI) => {
    const solutionItem = selectAnagramaSolution(thunkAPI.getState() as any, solution)
    if (solutionItem && !solutionItem.solved) {
      if (!solutionItem.partial) {
        const index = _.random(0, solutionItem.solution.length - 1)
        thunkAPI.dispatch(anagramaGameSlice.actions.buyLetter({solution, index}))

      } else {
        while (solutionItem.partial.includes('?')) {
          const index = _.random(0, solutionItem.solution.length - 1)
          if (solutionItem.partial[index] === '?') {
            thunkAPI.dispatch(anagramaGameSlice.actions.buyLetter({solution, index}))
            break;
          }
        }
      }
    }
  }
)

export interface AnagramaGameState extends AnagramaGame {}

const initialState: AnagramaGameState = {
  solutions: [],
  word: {
    anagram: '',
    currentWord: '',
    letters: [],
  },
}

export const anagramaGameSlice = createSlice({
  name: 'anagramaGame',
  initialState,
  // The `reducers` field lets us define reducers and generate associated actions
  reducers: {
    setAnagramaData: (state, action: PayloadAction<AnagramaGame>) => {
      return action.payload
    },
    clearLetters: (state) => {
      state.word.currentWord = ''
      state.word.letters = state.word.letters.map((l) => ({
        ...l,
        used: false,
      }))
    },
    selectLetter: (
      state,
      action: PayloadAction<{ letter: string; index?: number }>
    ) => {
      let { letter, index } = action.payload
      if (index === undefined) {
        index = state.word.letters.findIndex(
          (l) => !l.used && l.letter.toUpperCase() === letter.toUpperCase()
        )
      }
      if (index >= 0) {
        state.word.letters[index].used = true
        state.word.currentWord += letter.toUpperCase()

        //Checa se há soluções
        const sIndex = state.solutions.findIndex(
          (s) =>
            !s.solved &&
            s.normalizedSolution.toUpperCase() ===
              state.word.currentWord.toUpperCase()
        )
        if (sIndex >= 0) {
          state.solutions[sIndex].solved = true
          wordSolved(sIndex)
          state.word.currentWord = ''
          state.word.letters = state.word.letters.map((l) => ({
            ...l,
            used: false,
          }))
        }
      }
    },
    buyLetter: (
      state,
      action: PayloadAction<{ solution: string, index: number }>
    ) => {
      const {solution, index} = action.payload
      const solutionIndex = state.solutions.findIndex((s) => s.solution === solution)
      const solutionItem = state.solutions[solutionIndex]
      if (solutionItem && !solutionItem.solved) {
        if (index >= 0 && index < solutionItem.solution.length) {
          if (!solutionItem.partial) {
            solutionItem.partial = '?'.repeat(solution.length)
          }
          let splittedPartial = solutionItem.partial.split('')
          splittedPartial[index] = solution[index].toUpperCase()
          solutionItem.partial = splittedPartial.join("")
          if (!solutionItem.partial.includes('?')) {
            solutionItem.solved = true
            wordSolved(solutionIndex)
          }
        }
      }
    },
  },
  extraReducers: (builder) => {
    builder.addCase(shuffleLetters.fulfilled, (state, { payload }) => {
      state.word.letters = payload
    })
  },
})

function wordSolved(solutionIndex: number) {
  //const solution = document.getElementById("sol" + solutionIndex)
  //solution?.scrollIntoView({behavior: "smooth", block: "center", inline: "center"});
}

export const {
  setAnagramaData,
  clearLetters,
  selectLetter,
  buyLetter,
} = anagramaGameSlice.actions

export const selectAnagramaGame = (state: RootState) => state.anagramaGame
export const selectAnagramaWord = (state: RootState) => state.anagramaGame.word
export const selectAnagramaSolutions = (state: RootState) =>
  state.anagramaGame.solutions
export const selectAnagramaSolution = (state: RootState, solution: string) => state.anagramaGame.solutions.find((s) => s.solution === solution)

export default anagramaGameSlice.reducer
