import React from 'react'
import ClassNames from 'classnames'
import { useSelector } from 'react-redux'
import { selectBuyMode, toggleBuyMode } from '../features/anagramaGame/shopSlice'
import { useAppDispatch } from '../app/hooks'

export function BuyModeButtonComponent() {
  const buyMode = useSelector(selectBuyMode)
  const dispatch = useAppDispatch()


  const handleClick = () => {
    dispatch(toggleBuyMode())
  }


    return (
      <button
        onClick={handleClick}
        className={ClassNames([
          'btn',
          'btn-outline-primary',
          'float-end',
          {"active": buyMode}
        ])}
        style={{ lineBreak: 'loose', maxWidth: '10em', display: 'block' }}
      >
        <i className="bi bi-cart"></i>
      </button>
    )
}
